import React, { Component } from 'react';
import { List, InputItem, NavBar, Icon, Button,WhiteSpace, Toast } from 'antd-mobile';
import { createForm } from 'rc-form';
import 'antd-mobile/dist/antd-mobile.css';
import axios from 'axios';

function successToast() {
  Toast.success('login success !!!', 1);
}

function failToast() {
  Toast.fail('login failed !!!', 1);
}

class BasicInputExample extends Component {
  constructor(props) {
    super(props);
    this.state = {stuValue:'', pwdValue:'', isSuccess:false, isFalse:false};
    this.handleStuChange = this.handleStuChange.bind(this);
    this.handlePwdChange = this.handlePwdChange.bind(this);
    this.handleLogin = this.handleLogin.bind(this);
    this.showLoginMsg = this.showLoginMsg.bind(this);
  }

  showLoginMsg(msg){
    if(msg === true) {
      this.setState({ isSuccess:true })
    }
    else {
      this.setState({ isFalse:false })
    }
  }
  handleLogin() {
    axios({
      url:'http://148.70.185.36',
      method:'post'
    }).then(rep => { this.showLoginMsg(rep.msg) }).catch( error => {console.log("nono")})
  }
  handleStuChange(event) {
    this.setState({stuValue: event});
  }
  handlePwdChange(event) {
    this.setState({pwdValue: event});
  }
  render() {
    const { getFieldProps } = this.props.form;
    return (
      <div>
        <NavBar
          mode="light"
          onLeftClick={() => console.log('onLeftClick')}
          rightContent={[
          ]}
        >Login</NavBar>
        <br />
        <br />
        <List renderHeader={() => 'login'}>
          <InputItem
            {...getFieldProps('phone')}
            type="number"
            placeholder="学号"
            value={this.state.stuValue}
            onChange={this.handleStuChange}
          >学号</InputItem>
          <InputItem
            {...getFieldProps('password')}
            type="password"
            placeholder="密码"
            value={this.state.pwdValue}
            onChange={this.handlePwdChange}
          >密码</InputItem>
        </List>
        <WhiteSpace size="xl" />
        <Button type="primary" onClick={this.handleLogin}>登录</Button>
        <WhiteSpace size="xl" />
        <Button style={{ display:this.state.isSuccess ? "block":"none" }} onClick={successToast}>success</Button>
        <Button style={{ display:this.state.isFalse ? "block":"none" }} onClick={failToast}>fail</Button>
      </div>
    );
  }
}

export const BasicInputExampleWrapper = createForm()(BasicInputExample);
